:root {
  --clr-mainBackground: hsl(233, 47%, 7%);
  --clr-cardBackground: hsl(244, 38%, 16%);
  --clr-accent: hsl(277, 64%, 61%);
  --clr-accent-dark: hsla(275.5, 100%, 34.1%, 0.97);
  --clr-mainHeading-stats: hsl(0, 0%, 100%);
  --clr-mainParagraph: hsla(0, 0%, 100%, 0.75);
  --clr-statHeadings: hsla(0, 0%, 100%, 0.6);
}

:root {
  --ff-primary: "Inter", sans-serif;
  --ff-secondary: "Lexend Deca", sans-serif;
}

:root {
  --fs-primary-700: 2.2rem;
  --fs-primary-690: 1.7rem;
  --fs-primary-500: 1.5rem;
  --fs-primary-400: 0.9375rem;
  --fs-secondary-400: 0.9375rem;
}

:root {
  --fw-primary-regular: 400;
  --fw-primary-semiBold: 700;
  --fw-secondary-regular: 400;
}

