/****** Elad Shechter's RESET *******/
/*** box sizing border-box for all elements ***/
*,
*::before,
*::after {
  box-sizing: border-box;
}

a,
a:visited,
a:active {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}

button {
  background-color: transparent;
  color: inherit;
  border-width: 0;
  padding: 0;
  cursor: pointer;
}

figure {
  margin: 0;
}

input::-moz-focus-inner {
  border: 0;
  padding: 0;
  margin: 0;
}

ul,
ol,
dd {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-size: inherit;
  font-weight: inherit;
}

p {
  margin: 0;
}

cite {
  font-style: normal;
}

fieldset {
  border-width: 0;
  padding: 0;
  margin: 0;
}

body {
  font-family: var(--ff-primary);
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: var(--clr-mainBackground);
}

.main__heading {
  font-size: var(--fs-primary-700);
  font-weight: var(--fw-primary-semiBold);
  color: var(--clr-mainHeading-stats);
}
.main__heading .accent {
  color: var(--clr-accent);
}
.main__paragraph {
  line-height: 1.5;
  color: var(--clr-mainParagraph);
  font-size: var(--fs-primary-400);
}
.main__stats__stat {
  color: var(--clr-mainHeading-stats);
  font-size: var(--fs-primary-500);
  font-weight: var(--fw-primary-semiBold);
}
.main__stats__heading {
  color: var(--clr-statHeadings);
  font-family: var(--ff-secondary);
  font-size: var(--fs-secondary-400);
}

.main {
  display: flex;
  border-radius: 10px;
  overflow: hidden;
  background-color: var(--clr-cardBackground);
}
.main__container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 5em;
}
.main__heading {
  max-width: 18ch;
  margin-bottom: 0.5em;
}
.main__paragraph {
  max-width: 40ch;
  margin-bottom: 5em;
  line-height: 2;
}
.main__stats {
  display: flex;
  justify-content: space-between;
  text-transform: uppercase;
  flex-wrap: wrap;
  gap: 5px;
}
.main__img {
  position: relative;
  object-fit: cover;
  object-position: right center;
  height: 100%;
}
.main__img--container {
  position: relative;
}
.main__img--container::after {
  content: "";
  position: absolute;
  inset: 0 0 0 0;
  background-color: var(--clr-accent-dark);
  opacity: 0.5;
}

@media screen and (max-width: 25em) {
  body {
    text-align: center;
    padding: 2em;
  }

  .main {
    flex-direction: column;
  }
  .main__container {
    padding: 2em;
    align-items: center;
    order: 1;
  }
  .main__heading {
    font-size: var(--fs-primary-690);
    line-height: 1.5;
  }
  .main__paragraph {
    max-width: 29ch;
    margin-bottom: 3em;
  }
  .main__stats {
    flex-direction: column;
  }
  .main__stats > * {
    margin-bottom: 1em;
  }
  .main__stats__container > * {
    margin-bottom: 0.5em;
  }
}